import React, {useEffect, useState} from 'react';
import { m, n } from './helpers.jsx';
import _ from "lodash"
import { AreaChart, Area, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';

export default function Share(vals) {
  console.log(vals)
  const finalData     =  vals?.data
  const share          = vals.market
  const dailys         = vals?.dailyContrib?.stats;
  let datesdata        = '';
  const [porcentShare        , setPorcentShare        ] = useState(false);

  const getMarketShare = async () => {
    const finalDataParsed = Object.entries(finalData);

    const comparable = finalDataParsed.map(function(item, i){
      return { 
        date: item[0],
        val : item[1]/10000000000,
      }
    })

    datesdata = dailys?.map(function(date, i){
      return {
        dates: date.date.replace(', 2021', '')
      }
    });

    let amountdata = dailys.map(function(item, i){
      delete item.date;
      return item
    });

    let marketshareHistory = amountdata.map(function(item, i){
      return {
        dots : _.sum(_.values(amountdata[i])),
      }
      
    })

    const Finalcomparable = comparable.reduce((r, value, i) => {
      const { val, date } = value
      const cumVal = i === 0 ? 0 : r[i - 1].val
      const newDatum = { date, val: val + cumVal }
      r.push(newDatum);
      return r;
  }, []).map(function(item, i){
    return { 
      date : item.date,
      dots : item.val,
    }
  })


    let finalfinal = _.merge(datesdata, marketshareHistory);

    let marketInPorcent = finalfinal.map(function(items,m) {
      return {
        date : items.dates,
        val : ((Finalcomparable[m].dots/finalfinal[m].dots)*100)
      }
    })

    setPorcentShare(marketInPorcent);
  }

    useEffect(()=>{
      if(dailys) {
        getMarketShare()
      }
    }, [])
    
    const renderLegend = (props) => {
      const { payload } = props;
    
      return (
            payload?.map((entry, index) => (
              <p key={`item-${index}`} className="giant">
                <strong>{(entry.payload.val).toFixed(2)} % </strong>
                <small>({entry.payload.date})</small>
              </p>
            ))
      );
    }
  return (
    <>
      <div className="lockedTVL-container">
        {porcentShare &&
          <p class="giant porcentshare">
            <strong>{share.porcent}% </strong>
            <small>({porcentShare[(porcentShare.length) - 1].date })</small>
          </p>
        }
        <ResponsiveContainer className="lockedTVL" width="100%" height="100%">
          <AreaChart
            width={280}
            height={300}
            data={porcentShare}
          >
            <defs>
              <linearGradient id="colorUva" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5.23%" stopColor="#0C3EE3" stopOpacity={1}/>
                <stop offset="7.34%" stopColor="#0C3EE3" stopOpacity={1}/>
                <stop offset="97.88%" stopColor="rgba(255, 255, 255, 0.64)" stopOpacity={1}/>
                
              </linearGradient>
            </defs>       
            <XAxis dataKey="date" tick={{fontSize: 13}} />
            <Tooltip content={renderLegend} active={true} />
            <Area type="monotone" dataKey="val" strokeWidth="3" stroke="#0C3EE3" dot={false} fill="url(#colorUva)" />
          </AreaChart>
        </ResponsiveContainer> 
      </div>
    </>
  )
}